    // 引入三级联动
    import mpvueCityPicker from "../../components/mpvue-citypicker/mpvueCityPicker.vue";
    
    let sexOptions = ['未设置','男','女']; 
    let qgOptions = ['未设置','未婚','已婚']; 
    let jobOptions = ['未设置','IT','教师','服务业从业者','公务员','学生']; 
    
	export default {
        components:{
            mpvueCityPicker
        },
		data() {
			return {
				userPic:"../../static/demo/userpic/10.jpg",
                userName:"ATM",
                gender:"未设置",
                qinggan:"未设置",
                job:"未设置",
                birthday:" 未设置 ",
                cityPickerValueDefault:[0,0,1],//默认选中的地址
                pickerText:"未设置"
			}
		},
		methods: {
            // 三级联动
            showMulLinkageThreePicker() {
                this.$refs.mpvueCityPicker.show()
            },
            onConfirm(e) {
                this.pickerText = e.label;
            },
            // 修改头像
            changeImg(){
                uni.chooseImage({
                    count:1,
                    sizeType:["compressed"],
                    success: (res) => {
                        this.userPic = res.tempFilePaths;
                        // console.log(res.tempFilePath)
                    }
                })
            },
            // 单列选择
            changeOne(option){
               switch (option){
                    case "gender":
                        uni.showActionSheet({
                            itemList:sexOptions,
                            success: (res) => {
                               this.gender = sexOptions[res.tapIndex]
                            }
                        })
                       break;
                    case "qinggan":
                        uni.showActionSheet({
                            itemList:qgOptions,
                            success: (res) => {
                               this.qinggan = qgOptions[res.tapIndex]
                            }
                        })
                        break;
                    case "job":
                        uni.showActionSheet({
                            itemList:jobOptions,
                            success: (res) => {
                               this.job = jobOptions[res.tapIndex]
                            }
                        })
                        break;
                   default:
                        break;
                   
                }
            },
			submit(){
                
            },
            getDate(type) {
                const date = new Date();
                let year = date.getFullYear();
                let month = date.getMonth() + 1;
                let day = date.getDate();
    
                if (type === 'start') {
                    year = year - 60;
                } else if (type === 'end') {
                    year = year + 2;
                }
                month = month > 9 ? month : '0' + month;;
                day = day > 9 ? day : '0' + day;
                //格式化字符串
                return `${year}-${month}-${day}`;
            },
            //修改生日
             bindDateChange(e) {
                this.birthday = e.target.value
            },
		},
         computed: {
            startDate() {
                return this.getDate('start');
            },
            endDate() {
                return this.getDate('end');
            }
        },
        onBackPress() {
          if (this.$refs.mpvueCityPicker.showPicker) {
            this.$refs.mpvueCityPicker.pickerCancel();
            return true;
          }
        },
        onUnload() {
            if (this.$refs.mpvueCityPicker.showPicker) {
                this.$refs.mpvueCityPicker.pickerCancel()
            }
        },
	}
